<template>
  <div id="app">
    <div class="header">
      <img
        :src="require(`@/assets/${questions[currentQuestion].backgroundImage}`)"
      />
    </div>
    <div class="content">
      <progress-bar
        :current-step="currentQuestion + 1"
        :total-steps="questions.length"
        v-if="!isCompleted"
      ></progress-bar>

      <question
        :question-data="questions[currentQuestion]"
        :next-step="nextStep"
        v-if="!isCompleted"
      ></question>

      <div v-if="isCompleted" style="color: #616161">
        <div v-if="isLoading">
          <Loading-Icon></Loading-Icon>
          <h3>Cargando resultado...</h3>
        </div>

        <div v-if="!isLoading">
          <SmileyFaceIcon></SmileyFaceIcon>
          <h2 style="text-align: center">¡Estás llamado para servir!</h2>
          <div>
            <p>{{ verses[selectedVerse].text }}</p>
            <i>{{ verses[selectedVerse].chapter }}</i>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import questions from "./json/questions.js";
import progressBar from "./components/progress-bar.vue";
import question from "./components/question.vue";

import LoadingIcon from "./assets/loading.icon";
import SmileyFaceIcon from "./assets/smileyface.icon";

const verses = [
  {
    text: "Les hablo así hermanos, porque ustedes han sido llamados a ser libres; pero no se valgan de esa libertad para dar rienda suelta a sus pasiones. Más bien sírvanse unos a otros con amor.",
    chapter: "Galatas 5:13 (NVI)"
  },
  {
    text: "Entonces Jesús se sentó, llamó a los doce y les dijo: Si alguno quiere ser el primero, que sea el último de todos y el servidor de todos.",
    chapter: "Marcos 9:35 (NVI)"
  },
  {
    text: "Cada uno ponga al servicio de los demás el don que haya recibido, administrando fielmente la gracia de Dios en sus diversas formas.",
    chapter: "1 Pedro 4:10 (NVI)"
  },
  {
    text: "Así alumbre vuestra luz delante de los hombres, para que vean vuestras buenas obras, y glorifiquen a vuestro Padre que está en los cielos.",
    chapter: "Mateo 5:16 (RV1960)"
  },
  {
    text: "Pues Dios no es injusto. No olvidará con cuánto esfuerzo han trabajado para él y cómo han demostrado su amor por él sirviendo a otros creyentes como todavía lo hacen.",
    chapter: "Hebreos 6:10 (NTV)"
  },
  {
    text: "Si el que te aborrece tuviere hambre, dale de comer pan, y si tuviere sed, dale de beber agua.",
    chapter: "Proverbios 25:21 (RV1960)"
  },
];

export default {
  name: "App",
  components: {
    progressBar,
    question,
    LoadingIcon,
    SmileyFaceIcon
  },
  data() {
    return {
      questions: null,
      currentQuestion: null,
      verses: verses,
      isCompleted: false,
      isLoading: false,
      selectedVerse: null
    };
  },
  created() {
    const scope = this;
    this.currentQuestion = 0;

    const shuffledQuestions = this.shuffleArray(questions).splice(0, 5);
    this.questions = shuffledQuestions.map(function (question) {
      return {
        ...question,
        options: [...scope.shuffleArray(question.options)].splice(0, 4)
      }
    });

    this.selectedVerse = Math.floor(Math.random() * verses.length);
  },
  methods: {
    nextStep() {
      const next = this.currentQuestion + 1;
      if (next < this.questions.length) {
        this.currentQuestion = next;
      } else {
        const scope = this;

        this.isCompleted = true;
        this.isLoading = true;

        setTimeout(() => {
          scope.isLoading = false;
        }, 3000);
      }
    },
    shuffleArray: function (options) {
      for (let i = options.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        [options[i], options[j]] = [options[j], options[i]];
      }

      return options;
    }
  },
};
</script>

