<template>
  <div class="question-container">
    <div class="heading">
      {{ questionData.question }}
      {{ questionData.multiple ? "(multiple)" : "" }}
    </div>
    <div class="options">
      <div
        class="option"
        v-for="(option, index) in questionData.options"
        :key="index"
        :class="{ selected: selectedOptions.includes(index) }"
        @click="selectOption(index)"
      >
        {{ option }}
      </div>
    </div>

    <div class="next" @click="goNext">Siguiente</div>
  </div>
</template>

<script>
export default {
  name: "question",
  props: {
    questionData: Object,
    nextStep: Function,
  },
  data() {
    return {
      selectedOptions: [],
    };
  },
  methods: {
    goNext() {
      if (this.selectedOptions.length > 0) {
        this.selectedOptions = [];
        this.nextStep();
      }
    },
    selectOption(option) {
      if (this.questionData.multiple) {
        const index = this.selectedOptions.indexOf(option);
        index < 0 ? this.selectedOptions.push(option) : this.selectedOptions.splice(index, 1);
      } else {
        this.selectedOptions = [];
        this.selectedOptions[0] = option;
      }
    }
  },
};
</script>

<style scoped>
.question-container {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  row-gap: 2rem;
  justify-items: center;
  text-transform: uppercase;
}

.question-container > .heading {
  font-size: 1.5rem;
  font-weight: 600;
  text-align: center;
}

.question-container > .options {
  min-width: 75%;
  display: grid;
  grid-template-columns: 1fr;
  row-gap: 1rem;
}

.question-container > .options > .option {
  width: 100%;
  padding: 1rem;
  background-color: #ffffff;
  border: 1px solid #61c5a7;
  text-shadow: 0 0 1px rgba(0, 0, 0, 0.5);
  color: #7a7a7a;
  border-radius: 48px;
  text-align: center;
  cursor: pointer;
  transition: 0.2s linear;
}

.question-container > .options > .option:hover {
  background-color: #b2f0dd;
  border: 1px solid #b2f0dd;
  color: #7caf9f;
  transition: 0.2s linear;
}

.question-container > .options > .option.selected {
  background-color: #6dccb0;
  border: 1px solid #6dccb0;
  color: #ffffff;
  text-shadow: 0 0 1px rgba(0, 0, 0, 0.5);
  transition: 0.2s linear;
}

.question-container > .next {
  cursor: pointer;
  font-weight: 700;
  opacity: 0.5;
}

.question-container > .next:hover {
  text-decoration: underline;
  opacity: 1;
}
</style>