<template>
  <div class="progress-bar" :style="getTotalSteps">
    <div class="step" v-for="(step, index) in currentStep" :key="index"></div>
  </div>
</template>

<script>
export default {
  name: "progressBar",
  props: {
    currentStep: Number,
    totalSteps: Number,
  },
  computed: {
    getTotalSteps() {
      return {
        gridTemplateColumns: `repeat(${this.totalSteps}, 1fr)`,
      };
    },
  },
};
</script>

<style scoped>
.progress-bar {
  width: 75%;
  height: 8px;
  border-radius: 24px;
  background-color: #ccedff;
  display: grid;
  column-gap: 2px;
  overflow: hidden;
}

.progress-bar > .step {
  width: 100%;
  height: 8px;
  background-color: #3ba7f5;
}
</style>